.starred-frame {
  background: #ffffff;
  padding: 10px;
  height: 34.55px;
  position: relative;
  box-shadow: var(
    --shadow-level-6-box-shadow,
    0px 8px 16px 0px rgba(96, 97, 112, 0.16)
  );
  margin-top: 10px;
  width: 96%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}
.starred-1 {
  display: flex;
  flex-direction: row;
  gap: 5px;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  left: 30px;
  top: 10px;
}
.star-icon {
  flex-shrink: 0;
  width: 20px;
  height: 20px;
  position: relative;
  overflow: visible;
}
.star-file-icon {
  flex-shrink: 0;
  width: 40px;
  height: 40px;
  position: relative;
  overflow: visible;
  top: 5px;
}
.starred-content {
  color: var(--grey-500, #3a3c40);
  text-align: left;
  font-family: var(--body-8-font-family, "Montserrat-Medium", sans-serif);
  font-size: 16px;
  font-weight: var(--body-8-font-weight, 500);
  position: relative;
}
.owner {
  display: flex;
  flex-direction: row;
  gap: 4.33px;
  align-items: center;
  justify-content: flex-start;
  position: absolute;
  left: 353px;
  top: 17px;
}
.avater {
  flex-shrink: 0;
  width: 13px;
  height: 13px;
  position: relative;
}
.base {
  background: var(--gray-200, #e5e7eb);
  border-radius: 54.17px;
  border-style: solid;
  border-color: var(--common-white, #ffffff);
  border-width: 0.54px;
  width: 100%;
  height: 100%;
  position: absolute;
  right: 0%;
  left: 0%;
  bottom: 0%;
  top: 0%;
  box-shadow: var(
    --normal-shadow-s-1-box-shadow,
    2px 4px 24px 0px rgba(0, 0, 0, 0.05)
  );
}
.aa {
  color: var(--gray-800, #1f2937);
  text-align: center;
  font-family: var(
    --text-text-5-medium-font-family,
    "PublicSans-Medium",
    sans-serif
  );
  font-size: var(--text-text-5-medium-font-size, 14px);
  line-height: var(--text-text-5-medium-line-height, 20px);
  font-weight: var(--text-text-5-medium-font-weight, 500);
  position: absolute;
  left: calc(50% - 5.37px);
  top: calc(50% - 4.92px);
}
.kelvin {
  color: var(--grey-500, #3a3c40);
  text-align: left;
  font-family: "Montserrat-Medium", sans-serif;
  font-size: 7.0416669845581055px;
  font-weight: 500;
  position: relative;
}
.starred-date {
  color: var(--grey-500, #3a3c40);
  text-align: left;
  font-family: var(--body-8-font-family, "Montserrat-Medium", sans-serif);
  font-size: var(--body-8-font-size, 10px);
  font-weight: var(--body-8-font-weight, 500);
  position: absolute;
  left: 664px;
  top: 20px;
}
.starred-history-date {
    color: var(--grey-500, #3a3c40);
  text-align: left;
  font-family: var(--body-8-font-family, "Montserrat-Medium", sans-serif);
  font-size: var(--body-8-font-size, 10px);
  font-weight: var(--body-8-font-weight, 500);
  position: relative;
  width: 76px;
  margin-top: 20px;
}

