  box-sizing: border-box;
}
.arrow-wrapper {
  display: flex;
  flex-direction: row;
  gap: 7px;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  cursor: pointer;
  text-decoration: none;
}
.arrow-icon {
  flex-shrink: 0;
  width: 14px;
  height: 26px;
  position: relative;
  overflow: visible;
  cursor: pointer;
}
.arrow-back-text {
  color: #1c1b1f;
  text-align: center;
  font-family: "Montserrat-Regular", sans-serif;
  font-size: 20px;
  font-weight: 400;
  position: relative;
  cursor: pointer;
}

