}
.rectangle-26 {
  background: #ffffff;
  border-radius: 8px;
  width: 247px;
  height: 124px;
  position: absolute;
  left: 0.05px;
  top: 0px;
  box-shadow: 0px 2.66px 13.31px 0px rgba(175, 137, 255, 0.15);
}
.frame-3028 {
  width: 186px;
