}
.box-2-text-box2 {
  display: flex;
  flex-direction: column;
  gap: 0px;
  align-items: flex-start;
  justify-content: flex-start;
  position: absolute;
  left: 81px;
  top: 4.5px;
  text-decoration: none;
}
.split-to-pdf {
  color: #23272e;
