@media only screen and (max-width: 480px) {
  .convert-files-heading {
    text-align: center;
  }
  .convert-overview-container, .convert-overview-container-r2  {
    width: 95%;
    height: 67px;
    margin: 0px auto 0px auto;

  }
  .convert-box-1, .convert-box-2 {
    /* width: 102.96px; */
    width: 30%;
    height: 63.33px;

  }
  .con-box1-border, .con-box-2-border {
    width: 100%;
    height: 100%;
  }
  .box-1 {
    top: 10px;
    left: 5px;
  }
  .box-1-top {
    width: 30px;
    height: 30px;
  }
  .box-1-circle, .box-2-circle {
    width: 27px;
    height: 27px;
    top: 4px;
    left: 0px;
  }
  .box-1-image, .box-2-image {
    width: 16px;
    height: 16px;
    top: 10px;
    left: 5px;
  }
  .convert-title {
    font-size: 8px;
    right: 12px;
    top: 5px;
  }
  .convert-sub-title {
    font-size: 6.5px;
    top: 5px;
    right: 12px;
  }
}

