.frame-512,
.frame-512 * {
  box-sizing: border-box;
}
.frame-512 {
  display: flex;
  flex-direction: row;
  gap: 16px;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  overflow: hidden;
}
.frame-511 {
  flex-shrink: 0;
  width: 248px;
  height: 210px;
  position: relative;
}
.rectangle-24 {
  background: #35b2f6;
  border-radius: 8px;
  width: 248px;
  height: 213px;
  position: absolute;
  left: 0px;
  top: 0px;
  box-shadow: 0px 2.66px 13.31px 0px rgba(175, 137, 255, 0.15);
}
.ellipse-148 {
  background: #ffffff;
