/* Small mobile devices */
@media only screen and (max-width: 480px) {
  .frame-512 {
    width: 95%;
    margin: 0 auto;
  }
  .frame-511, .frame-506, .frame-506 {
    /* width: 100px; */
    width: 30%;
    height: 89px;
    margin: 0px;
  }
  .rectangle-24, .rectangle-25 {
    /* width: 110px; */
    width: 100%;
    height: 90px;
    /* border: 2px solid red; */

  }
  .ellipse-148, .ellipse-1482 {
    width: 28px;
    height: 28px;
    top: 10px;
    left: 10px;
  }
  .received-files, .shared-files {
    font-size: 7.64px;
    top: 19px;
    left: 42px;
  }
  ._2-580, ._1-780 {
    font-size: 11.89px;
    left: 70px;
    top: 67px;
  }
  .bxs-file, .bxs-share {
    width: 70px;
    height: 78px;
    left: 105px;
    top: 0px;
  }
  .pepicons-pencil-file2 {
    width: 15px;
    height: 15px;
    left: 17px;
    top: 15px;
  }
  .group2 {
    width: 15px;
    height: 15px;
    top: 0px;
    left: 0px;
  }
}

