/* Continuous clockwise rotation */
@keyframes rotateClockwise {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Class to apply continuous rotation */
.rotate {
  animation: rotateClockwise 4s linear infinite; /* Infinite loop */
}
}

